<template>
  <page-box>
     <!-- :title="title" :img="article.poster" -->
    <Header title="Archives"></Header>
    <!-- <div class="my-info">
      <img :src="avatar" alt="">
      <p>{{ time }}</p>
    </div> -->
    <div class="main-area">
      <loading class="article-loading" v-if="!isShow"></loading>
      <transition name="fade">
        <div class="archives">
          <ul>
            <li class="tips top">一共{{ num }}篇哦，继续加油！</li>
            <li v-for="(todo, index) in articleList" :key="index" :class="todo.class">
              <template v-if="todo.class == 'article'">
                <!-- {{ calcTime(todo.val.date) }}---{{ todo.val.title }} -->
                <span class="list-time">{{ calcTime(todo.val.date) }}</span>
                <!-- <span class="list-title">{{ todo.val.title }}</span> -->
                <router-link class="list-title" tag="span" :to="{ path: '/' + todo.val.filename }">{{ todo.val.title }}</router-link>
              </template>
              <template v-else>
                {{ todo.val }}
              </template>
            </li>
            <li class="tips bottom">一切从这里开始...</li>
          </ul>

        </div>
      </transition>
    </div>
  </page-box>
</template>

<script>
import Header from '@/components/Header'
import Loading from '@/components/Loading'
import pageBox from '@/components/PageBox'

import data from '@/data'
import tools from '@/utils/public'

export default {
  name: 'articlePage',
  components: {
    Header, Loading, pageBox
  },
  data() {
    return {
      isShow: false,
      articleList: [],
      num: 0
    }
  },
  mounted() {
    this.getArticleList();
  },
  methods: {
    getArticleList() {
      data.getAllList().then(data => {
        this.isShow = true;
        this.num = data.length;
        this.articleList = this.calcList(data);
      })
    },
    calcList(list) {
      let year = list[0].date.replace(/(\d{4}).*/, '$1');
      let res = [];

      res.push({
        class: 'year',
        val: year
      });

      list.forEach(item => {
        if(year != item.date.replace(/(\d{4}).*/, '$1')) {
          year = item.date.replace(/(\d{4}).*/, '$1');
          res.push({
            class: 'year',
            val: year
          });
        }

        res.push({
          class: 'article',
          val: item
        });
      });

      return res;
    },
    calcTime(date) {
      return tools.calcTime(date, 'tiny');
    }
  }

}
</script>

