import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home'
import article from '@/pages/article'
import archives from '@/pages/archives'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/archives',
      name: 'archives',
      component: archives,
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/:name',
      name: 'article',
      component: article,
      meta: {
        keepAlive: false
      }
    }
  ]
})